.products {

    &__tags {
        display: flex;
        flex-wrap: wrap;
    }

    &__tag {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 9.5rem;
        height: 3rem;
        font-weight: 600;
        font-size: 1.2rem;
        line-height: 11px;
        letter-spacing: 0.02em;
        color: #FFFFFF;

        @media(--mobile-screen) {
            width: 5.1rem;
            height: 1.6rem;
            font-weight: 600;
            font-size: .9rem;
        }

        &.akciya {
            background: #FF5C5C;
        }

        &.hit {
            background: #4438C9;
        }
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        @media(--mobile-screen) {
            margin-top: 0rem;
           }
    }

    &__item {
        width: calc(100%/3-1rem);
        box-shadow: var(--shadow);
        margin-bottom: 1.5rem;
        @media(--mobile-screen) {
            width: 48%;
           }

           &.catalog {
               width: 31%;
               @media(--mobile-screen) {
                width: 100%;
              }
              .products__image {
                @media(--mobile-screen) {
                    height: 22rem;
                  }
              }

              .products__title {
                  text-align: left;
                font-weight: 600;
                font-size: 1.4rem;
                line-height: 120%;
                letter-spacing: 0.02em;
                font-feature-settings: 'pnum' on, 'lnum' on;
                color: #000000;
                padding: .9rem 1.2rem;
              }
           }
    }

    &__item.sales {
        width: calc(65.5%);
        box-shadow: var(--shadow);
        margin-bottom: 1.5rem;
        @media(--mobile-screen) {
            width: 47%;
           }
        .products__title {
            background: #FF5050;
            color: var(--colorWhite);
        }
    }


    &__image {
        width: 100%;
        height: 13.2rem;
        overflow: hidden;
        img {
            width: 100%;
            object-fit: cover;
            transform: scale(1.1);
        }
    }

    &__title {
        padding: 1.3rem 0;
        background: var(--colorWhite);
        font-weight: 700;
        font-size: 1rem;
        line-height: 12px;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 0.02em;
        color: #000000;
    }

    &__head {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-bottom: 1rem;
    }

    &__filter {
        width: fit-content;

        &_wrap {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            margin-bottom: 2.4rem;
            margin-left: 2.4rem;
            @media(--mobile-screen) {
                width: 100%;
                display: flex;
                margin-left: 0;
                justify-content: space-between;
                margin-bottom: 0;
            }
        }

        &_control {
            width: max-content;
            background: #FFFFFF;
            border: 1px solid #DEDEDE;
            padding: 1.1rem 1.8rem;
            box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
            margin-left: .8rem;

            @media(--mobile-screen) {
                margin-left: 0;
                font-size: 1rem !important;
                padding: 1rem 1.4rem;
            }
        }

        
    }

    &__button {
        margin-bottom: 2.4rem;
        margin-left: auto;
        @media(--mobile-screen) {
            display: none;
        }

        &_mobile {
            display: none;
            @media(--mobile-screen) {
                display: block;
                text-align: center;
                max-width: 65%;
                margin: 0 auto;
            }

        }
    }

    &__slider {

        margin-bottom: 2.4rem;

        .products__image {
            position: relative;
            height: 17rem;

            img {
                
            }

            .products__price {
                display: block;
                position: absolute;
                bottom: .7rem;
                left: 1.2rem;
                font-weight: 600;
                font-size: 1.2rem;
                line-height: 120%;
                letter-spacing: 0.02em;
                text-transform: uppercase;
                font-feature-settings: 'pnum' on, 'lnum' on;
                color: #FFFFFF;
            }
        }

    }

    &__slider {
        position: relative;

        &:last-child {
            @media(--mobile-screen) {
                display: none;
            }
        }

        .owl-nav, .owl-dots {
            display: block !important;

            .owl-prev {
                position: absolute;
                top: 50%;
                left: -2.5rem;
                transform: translateY(-50%);
                color: transparent;
                transition: all .3s;
                backdrop-filter: grayscale(70%);
                opacity: .5;
                filter: grayscale(50%);
                @media(--mobile-screen) {
                    top: auto;
                    bottom: -10.6rem;
                    left: 0rem;
                    filter: grayscale(0);
                    opacity: 1;
                }
                &:hover {
                    opacity: 1;   
                    filter: grayscale(0);
                }

                &:after {
                    content: url('../img/icons/blue-arrow.svg');
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 5rem;
                    height: 5rem;
                    background: var(--colorWhite);
                    box-shadow: var(--shadow);
                    
                }
            }

            .owl-next {
                position: absolute;
                top: 50%;
                right: -2.5rem;
                transform: translateY(-50%);
                color: transparent;
                transition: all .3s;
                opacity: .5;
                filter: grayscale(50%);

                @media(--mobile-screen) {
                    top: auto;
                    bottom: -10.6rem;
                    right: 0rem;
                    filter: grayscale(0);
                    opacity: 1;
                }
                &:hover {
                    opacity: 1;   
                    filter: grayscale(0);
                }

                &:after {
                    content: url('../img/icons/blue-arrow.svg');
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 5rem;
                    height: 5rem;
                    background: var(--colorWhite);
                    box-shadow: var(--shadow);
                    transform: rotate(180deg);
                }
            }
        }
    }

    &__slide {
        margin: .7rem 0;
        // margin-right: 3rem;
        box-shadow: var(--shadow);

        .products__title {
            
            font-weight: 600;
            font-size: 14px;
            line-height: 120%;
            letter-spacing: 0.02em;
            text-align: start;
            font-feature-settings: 'pnum' on, 'lnum' on;
            padding-left: 1.2rem;
            padding-right: 1.2rem;
            color: #000000;
        }
    }

}

.products__image {
    position: relative;
    height: 17rem;

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        -o-object-fit: cover;
        object-fit: cover;
    }

    .products__price {
        display: block;
        position: absolute;
        bottom: .7rem;
        left: 1.2rem;
        font-weight: 600;
        font-size: 1.2rem;
        line-height: 120%;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #FFFFFF;
    }
}